import React, { useEffect, useRef } from 'react'
import { bounceBalls, initBalls } from '../helpers/bounceBalls'
import { titleEffect } from '../helpers/titleEffect';

export const Hero = () => {

    const subtitle = useRef( null );
    const balls = useRef( null );

    useEffect(() => {
        const { x, y, sentido} = initBalls();

        const intervalID = setInterval(()=>{
            bounceBalls({ balls: balls.current.children, x, y, sentido});
        },10);

        return ()=> clearInterval( intervalID );
    },[]);

    useEffect(()=>{
        const intervalID = setInterval(()=>{
            titleEffect( subtitle );
        },200);

        return ()=> clearInterval( intervalID );
    },[]);

    return (
        <>
        <div className="hero">
            <div className="main-title">
                <h1>Daniel Romero</h1>
                <h2 ref={ subtitle }>Fullstack developer</h2>      
            </div>
        </div>
        <div className="balls" ref={ balls }>
            <div className="ball calor"></div>
            <div className="ball agua"></div>
            <div className="ball mora"></div>
            <div className="ball muerdes"></div>
            <div className="ball rosa"></div>
            <div className="ball space"></div>
        </div> 
        </>
    )
}
