//const subtitle = document.querySelector('#subtitle');
let text = ['Fullstack developer','node js','react js','mongo db','redux','express','graphql'];
let tempText = text[0];
let palabraIndex = 0;

export const titleEffect = ({ current : subtitle}) =>{

    
    if( tempText.length < 1){
    
        if( palabraIndex < text.length-1 ){
          palabraIndex++;
        }else{
          palabraIndex = 0;
        }
        tempText = text[ palabraIndex ]
        
    }else{
        tempText = tempText.slice(tempText[ tempText.length -1], tempText.length -1);
    }
    subtitle.innerText = tempText;
}

export const initTitleEffect = () =>{
    const subtitle = document.querySelector('#subtitle');
    let text = ['Fullstack developer','node js','react js','mongo db','redux','express','graphql'];
    let tempText = text[0];
    let palabraIndex = 0;
    return { subtitle, text, tempText, palabraIndex  };
}